import React from 'react';
export class NET extends React.Component{
    constructor(props){
        super(props);
    }
    getTitle(){
        return(<h2>.NET</h2>);
    }
    getBody(){
        return (
        <>
        <h3>Habilidades</h3>
        <h4>Blazor</h4>
        <p>
          - Desarrollo de aplicaciones web progresivas (PWA) del lado del cliente (WebAssembly).<br/>
          - Autorizacion y autenticacion.<br/>
          - Consulta a endpoints.<br/>
        </p>
        <h4>.NET MVC ( .NET Framework / .NET Core )</h4>
        <p>
          - Desarrollo de aplicaciones web siguiendo el patron MVC utilizando modelos, vistas y controladores.<br/>
          - Generacion y actualizacion de modelos, vistas y controladores.<br/>
          - Entity Framework / Entity Framework Core para acceso a base de datos.<br/>
          - SignalR para actualizaciones en tiempo real del lado del cliente.<br/>
          - Consulta a endpoints.
        </p>
        <h4>Web Forms / Razor Pages</h4>
        <p>
          - Desarrollo de aplicaciones web enfocadas a formularios.<br/>
          - Entity Framework / Entity Framework Core para acceso a base de datos.<br/>
          - Consultas a bases de datos mediante SQLCommands.<br/>
        </p>
        </>
        );
    }
    render(){
        return (
            (this.props.type == 'title' && this.getTitle()) || (this.props.type == 'body' && this.getBody())
        );
    }
}
