export class ReactJS extends React.Component{
    constructor(props){
        super(props);
    }
    getTitle(){
        return(<h2>React</h2>);
    }
    getBody(){
        return (
        <>
        <h3>Habilidades</h3>
        <p>
          - Desarrollo de aplicaciones web del lado del cliente con React.<br/>
          - Generacion de componentes.<br/>
          - Implementacion de los ciclos de vida mediante los metodos correspondientes y mediante el hook useEffect.<br/>
          - Manejo de estados con y sin uso del hook useState.<br/>
          - Enrutado mediante la dependencia react-router-dom.<br/>
          - Consultas a endpoints.<br/>
          - Dynamic imports para la optimizacion en la carga de componentes.<br/>
        </p>
        </>
        );
    }
    render(){
        return (
            (this.props.type == 'title' && this.getTitle()) || (this.props.type == 'body' && this.getBody())
        );
    }
}