import React from 'react';
export class CSharp extends React.Component{
    constructor(props){
        super(props);
    }
    getTitle(){
        return(<h2>C#</h2>);
    }
    getBody(){
        return (
        <>
        <h3>Habilidades</h3>
        <p>
          - Programacion orientada a objetos.
          <br/>
          - Tareas paralelas.
          <br/>
          - Generacion y lectura de archivos.
          <br/>
        </p>
        </>
        );
    }
    render(){
        return (
            (this.props.type == 'title' && this.getTitle()) || (this.props.type == 'body' && this.getBody())
        );
    }
}
